<template>
  <v-card outlined dark style="margin: 0 0 10px 0; border-radius: 1em">
    <v-card-title>{{this.name}}</v-card-title>
    <v-card-subtitle>{{this.walk.date}}</v-card-subtitle>

    <v-expansion-panels :disabled="!this.expandable">
      <v-expansion-panel dark hover focusable inset :key="this.index" :style="{color: 'white', background: this.expandable ? 'rgba(255,255,255,0.075)' : null, 'border-radius': '0 0 1em 1em'}">
        <v-expansion-panel-header style="line-height: 1.25">
          {{this.fromTo}}<br>
          {{this.headCount}}<br>
          {{this.km}}
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <table>
            <tr v-for="h in this.walk.checkpoints">
              <td class="suhclogtime">{{ h.time }}</td>
              <td>{{ h.event }}</td>
            </tr>
          </table>
          <p v-for="account in this.walk.accounts"><i><br>{{ account.who }} says... <br></i>{{ account.what }}</p>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
  </v-card>
</template>

<script>
  export default {
    name: 'SUHCLogEntry',
    props: ['walk', 'index'],

    data: () => ({
      name: '',
      fromTo: '',
      headCount: '',
      km: '',
    }),

    computed: {
      expandable () {
        return this.walk.checkpoints || this.walk.accounts;
      }
    },

    mounted () {
      if (this.walk.name) {
        this.name = this.walk.name;
        if (this.walk.start && this.walk.finish) {
          if (this.walk.start === this.walk.finish) {
            this.fromTo = 'Around ' + this.walk.start;
          } else {
            this.fromTo = 'From ' + this.walk.start + ' to ' + this.walk.finish;
          }
        }
      } else
        if (this.walk.start === this.walk.finish) {
          this.name = 'Day walk around ' + this.walk.start;
        } else {
          this.name = 'Day walk from ' + this.walk.start + ' to ' + this.walk.finish;
        }

      if (this.walk.headCount) {
        this.headCount = this.walk.headCount + ' attended';
        if (this.walk.committeeCount) {
          this.headCount += ' (' + this.walk.committeeCount + ' committee)';
        }
      }
      if (this.walk.km) {
        this.km = 'Effective walk length: ' + this.walk.km + ' km'
      }
    }
  }
</script>
