<template>
  <div>
    <h2>SUHC Captain's Log</h2>

    <P v-if="!this.loadState" style="text-align: justify">
     I have held the position of Day Walk Secretary of SUHC roughly between March 2017 and May 2018. The idea of keeping a (sometimes
     unnecessarily detailed) log of our events occurred to me before that and will last until long after, as far as I can attend; of
     course, said attendance decreased drastically after leaving uni. Here goes:</P>

    <P v-if="this.loadState" style="text-align: center">
     {{this.loadState}}</P>

    <SUHCLogEntry
                v-for="walk, i in walks"
                :walk="walk"
                :index="walks.length - i"
              />
  </div>
</template>

<script>
  import axios from 'axios';
  import SUHCLogEntry from '../components/SUHC-LogEntry.vue';

  export default {
    name: 'SUHC_Log',
    components: {
      SUHCLogEntry
    },

    data: () => ({
      loadState: 'Loading...',
      walks: []
    }),
    mounted () { axios({ method: 'GET', url: this.$dataUrl + '/suhc/log.json' })
      .then(result => { /* console.log('Result data', result.data); */ this.walks = result.data; this.loadState=''; },
            error  => { console.error(error); this.loadState='Error!'; })}
  }
</script>
